import React from 'react';
import moment from 'moment-timezone';

interface Props {
  cover: string;
  tag?: string;
  title: string;
  author: string;
  timestamp: Date;
  location: string;
}

export function WorkshopCard(props: Props): JSX.Element {
  const date = moment(props.timestamp).utc();
  const formattedTime = `${date.tz('America/New_York').format('h a z')} / ${date
    .tz('America/Los_Angeles')
    .format('h a z')}`
    .replace('PDT', 'PST')
    .replace('EDT', 'EST');
  const formattedDate = `${date.format('dddd, MMM DD')}th`;
  return (
    <div className="card workshop-card">
      <div className="card-header">
        <img src={props.cover} alt={props.title} className="image" />
        {props.tag && <div className="info-tag">{props.tag}</div>}
      </div>
      <div className="card-body">
        <div className="title">{props.title}</div>
        <div className="author">{props.author}</div>
        <div className="workshop-info-box">
          <div className="workshop-info">
            <span className="material-icons icon">event</span>
            <span className="text">{formattedDate}</span>
          </div>
          <div className="workshop-info">
            <span className="material-icons icon">watch_later</span>
            <span className="text">{formattedTime}</span>
          </div>

          <div className="workshop-info">
            <span className="material-icons icon">location_on</span>
            <span className="text">{props.location}</span>
          </div>
        </div>
        <div className="card-action">
          <button type="button" className="btn btn-primary detail-button">
            See Details
          </button>
          <button type="button" className="btn btn-light-success edit-button">
            <span className="material-icons text-success">edit</span>
            <span>Edit</span>
          </button>
        </div>
      </div>
    </div>
  );
}
