import React, { useState } from 'react';
import Logo from '../../assets/images/logo.png';
import clsx from 'clsx';
import PersonalDetails from './components/PersonalDetails';
import SchoolDetails from './components/SchoolDetails';

const SignUpAsEducator = (): JSX.Element => {
  const [signUpType, setSignUpType] = useState('Personal');

  return (
    <div className="instructor-form">
      <div className="form-showcase">
        <img src={Logo} alt="" />
        <h1>SignUp As Educator</h1>
        <p>Create an account to see the workshops you are going to attend</p>
      </div>
      <div className="signup-type-btns">
        <button
          type="button"
          className={clsx({ active: signUpType === 'Personal' })}
          onClick={() => setSignUpType('Personal')}>
          Personal Details
        </button>
        <button
          type="button"
          className={clsx({ active: signUpType === 'School' })}
          onClick={() => setSignUpType('School')}>
          School Details
        </button>
      </div>
      {signUpType === 'Personal' && (
        <>
          <PersonalDetails />
        </>
      )}
      {signUpType === 'School' && (
        <>
          <SchoolDetails />
        </>
      )}
    </div>
  );
};

export default SignUpAsEducator;
